<template>
  <transition name="fade" mode="in-out">
    <div class='forumPage'>
      <v-Header class="head">
        <span slot='text'>社区</span>
      </v-Header>
      <nav class='forumType'>
        <ul>
          <template v-for="(row,$index) in tabList">
            <li :class="{active:select===row.tagId}" @click='changeType(row.tagId)' :key='$index'>{{row.tagName}}</li>
          </template>          
        </ul>
      </nav>
      <hr>
      <div id='forumListScroll'>
        <v-forumList :forumLists='forumLists'/>
      </div>
      <div class='sendPostBtn'>我要发帖</div>
    </div>
  </transition>
</template>
<script>
import Header from '../../components/header';
import BScroll from 'better-scroll';
import forumList from '../forum/forumList';
export default {
    name:'',
    props:[],
    components:{
      'v-Header':Header,
      'v-forumList':forumList
    },
    data () {
        return {
          tabList:['首页','摄影','装备','行程出让'],
          select:0,
          lastDiscussId:0,
          scroll:'',
          forumLists:[]
        }
    },
    watch:{
      select(n,o){
        console.log(n,o)
        this.lastDiscussId = 0;
        this.forumLists = [];
        this.getDiscussList();
      }
    },
    methods:{
      changeType(data){
          this.select=data;
      },
      getDiscussTag(){
        this.$http.get(this.$port.DISCUSS_TAG_PROT,{
				}).then((response) => {
                    const {data:{data}} = response;
					if(response.data.code == 200){
            console.log(data)
            this.tabList=data;
            this.select = data[0].tagId;
					}
				})
				.catch((error) => {
					console.log(error.response.data);
				});
      },
      getDiscussList(){
        this.$http.get(`${this.$port.DISCUSS_LIST_PROT}?tagId=${this.select}&lastDiscussId=${this.lastDiscussId}`,{
				}).then((response) => {
                    const {data:{data}} = response;
                    console.log(data)
					if(response.data.code == 200){
            this.forumLists = [...this.forumLists,...data];
            console.log(this.forumLists)
					}
				})
				.catch((error) => {
					console.log(error.response.data);
				});
      },

    },
    mounted() {
      const wrapper = document.querySelector('#forumListScroll');
      this.scroll = new BScroll(wrapper, {
        click: true,
        // pullDownRefresh: true,
        // pullUpLoad: true,
      });
      this.getDiscussTag();
    },
}
</script>

<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.forumPage{
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height:100%;
  overflow:hidden;
  background:white;
  .forumType{
    width:6.9rem;
    height:0.87rem;
    margin:0 0.3rem;
    font-size:0.32rem;
    line-height:0.87rem;
    color:#666666;
    li{
      float:left;
      width:25%;
      font-weight: bold;
      &.active{
        color:#1797c6;
      }
    }
  }
  #forumListScroll{
    position: absolute;
    top: 1.71rem;
    left: 0;
    right:0;
    bottom:0.88rem;
    margin:auto;
    overflow:hidden;
  }
  hr{
    width:100%;
    height: 0.02rem;
    background: #f2f2f2;
    }
  .sendPostBtn{
    position:fixed;
    bottom:0;
    left:0;
    height:0.88rem;
    width:100%;
    line-height:0.88rem;
    text-align:center;
    background:url('../../assets/images/publishBtn.jpg') no-repeat;
    background-size:cover;
    color:white;
    font-size:0.36rem;
    font-weight: bold;
  }
}
</style>




